.card {
  width: 291px;
  height: 555px;
  flex-shrink: 0;
  border-radius: 31px;
  background: #FFF;
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.09);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 14px 14px 0 14px;
  position: relative;
}

.imageContainer {
  width: 264px;
  height: 338px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 23px;
  background: #A5C365;
  box-shadow: 0px 0px 11.7px 0px rgba(0, 0, 0, 0.25) inset;
  overflow: hidden;
  position: relative;
  margin-bottom: 20px;
}

.imageFrame {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.media {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contentContainer {
  width: 100%;
  padding: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  color: #000;
  font-family: "SF Pro Text", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: -1.2px;
  margin-top: 0;
  margin-bottom: 7px;
  text-align: center;
}

.description {
  color: #000;
  font-family: "SF Pro Text", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.7px;
  margin-top: 0;
  margin-bottom: 8px;
  text-align: center;
}

.timeEstimate {
  color: rgba(0, 0, 0, 0.28);
  font-family: "SF Pro Text", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-bottom: 9px;
  text-align: right;
  align-self: flex-end;
  padding-right: 14px;
}

.footer {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: auto;
  padding-bottom: 20px;
}

.startButton {
  width: 264px;
  height: 50px;
  flex-shrink: 0;
  border-radius: 58px;
  background: #000;
  border: none;
  cursor: pointer;
  transition: opacity 0.2s ease;
  color: #FFF;
  font-family: "SF Pro Text", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  letter-spacing: -1.2px;
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 20px;
}

.startButton span {
  flex: 1;
  text-align: center;
}

.startIcon {
  width: 29px;
  height: 29px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.startButton:hover {
  opacity: 0.9;
} 