/* 버튼 스타일 추가 */
button {
  background-color: white;
  color: #282c34;
  border: none;
  padding: 12px 30px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-bottom: 10px;
}

button:hover {
  background-color: #f0f0f0;
}

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #282c34;
  color: white;
}

.header {
  text-align: center;
  padding: 20px;
}

.title {
  font-size: 2.5rem;
  margin-bottom: 2rem;
}

.buttonContainer {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 32px;
}

.button {
  background-color: white;
  color: #282c34;
  border: none;
  padding: 12px 30px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s;
  margin-bottom: 10px;
  width: 300px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  line-height: 1.4;
}

.button:hover {
  background-color: #f0f0f0;
  transform: scale(1.05);
}

